.app__education {
  flex: 1;
  width: 100%;
  flex-direction: column;
}
.education__section {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin-top: 3rem;
  &-container {
    cursor: pointer;
    width: 80%;
    display: flex;
    justify-content: flex-start;
    box-shadow: rgba(0, 0, 0, 0.09) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px,
      rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px,
      rgba(0, 0, 0, 0.09) 0px 32px 16px;
    border-radius: 2rem;
    margin: 2rem 0;
    max-height: 250px;
    transition: transform 1s;
    @media screen and (max-width: 1050px) {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      max-height: 800px;
    }

    &:hover {
      transform: scale(1.1);
    }
    &_img {
      flex: 0.5;
      margin: 2rem;

      img {
        height: 100%;
        width: 100%;
      }
    }
    &_text {
      flex: 1;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
      &-date {
        margin: 1rem 0;
        background: red;
        border-radius: 2rem;
        p {
          color: #fff;
          margin: 0.5rem 1rem;
        }
      }
      &-degree {
        margin-bottom: 2rem;
        font-size: 40px;
        font-family: "Times New Roman", Times, serif;
        h4 {
          font-weight: 500;
        }
        @media screen and (max-width: 1200px) {
          font-size: 30px;
        }
        @media screen and (max-width: 650px) {
          font-size: 20px;
        }
        @media screen and (max-width: 550px) {
          font-size: 17px;
        }
      }
      &-institute {
        p {
          font-size: 20px;
          font-weight: 600;
          @media screen and (max-width: 650px) {
            font-size: 15px;
          }
          @media screen and (max-width: 550px) {
            font-size: 12px;
          }
        }
      }
      &-address {
        p {
          font-size: 20px;
          font-weight: 600;
          margin-bottom: 2rem;
          @media screen and (max-width: 650px) {
            font-size: 15px;
          }
          @media screen and (max-width: 550px) {
            font-size: 12px;
          }
        }
      }
    }
  }
}
